import * as browser from 'webextension-polyfill';
import Config from '../scripts/Config';
import StorePouchDB from '../scripts/StorePouchDB';
import StoreRestApi from '../scripts/StoreRestApi';
import StoreTalismanApi from '../scripts/StoreTalismanApi';
import Sitemap from '../scripts/Sitemap';
import Queue from '../scripts/Queue';
import ChromePopupBrowser from '../scripts/ChromePopupBrowser';
import Scraper from '../scripts/Scraper';
import getBackgroundScript from '../scripts/BackgroundScript';
import urlJoin from 'url-join';

const config = new Config();
let store;
let manualTargetTabId = null; // Allow manual tab selection

async function talismanAuthListener(responseDetails) {
	async function reloadTabs() {
		const openTabs = await browser.tabs.query({ url: urlJoin(config.talismanApiUrl, '/*') });
		openTabs.forEach(tab => {
			if (tab.id !== responseDetails.tabId) {
				browser.tabs.reload(tab.id);
			}
		});
	}

	const loginUrl = urlJoin(config.talismanApiUrl, '/oauth/login');
	const logoutUrl = urlJoin(config.talismanApiUrl, '/oauth/logout');
	if (responseDetails.url === loginUrl || responseDetails.url === logoutUrl) {
		await reloadTabs();
		if (responseDetails.tabId !== -1) {
			await browser.runtime.sendMessage({ authStatusChanged: true });
		}
	}
}

function setStore() {
	browser.webRequest.onCompleted.removeListener(talismanAuthListener);
	if (config.storageType === 'rest') {
		store = new StoreRestApi(config, config.restUrl);
	} else if (config.storageType === 'talisman') {
		store = new StoreTalismanApi(config, config.talismanApiUrl);
		browser.webRequest.onCompleted.addListener(talismanAuthListener, {
			urls: [urlJoin(config.talismanApiUrl, '/oauth/*')],
		});
	} else {
		store = new StorePouchDB(config);
	}
}

config.loadConfiguration().then(() => {
	console.log('initial configuration', config);
	setStore();
});

// Open scraper tab when extension icon is clicked
browser.action.onClicked.addListener(async () => {
	const tabUrl = browser.runtime.getURL('tab/tab.html');
	// Check if tab is already open
	const existingTabs = await browser.tabs.query({ url: tabUrl });
	if (existingTabs.length > 0) {
		// Focus existing tab
		await browser.tabs.update(existingTabs[0].id, { active: true });
		await browser.windows.update(existingTabs[0].windowId, { focused: true });
	} else {
		// Open new tab
		await browser.tabs.create({ url: tabUrl });
	}
});

browser.storage.onChanged.addListener(function () {
	config.loadConfiguration().then(async () => {
		console.log('configuration changed', config);
		setStore();
	});
});

// URLs that don't support content scripts
const UNSUPPORTED_URL_PATTERNS = [
	/^chrome:\/\//,
	/^chrome-extension:\/\//,
	/^about:/,
	/^edge:\/\//,
	/^brave:\/\//,
	/^opera:\/\//,
	/^moz-extension:\/\//,
];

function isUrlSupported(url) {
	if (!url) return false;
	return !UNSUPPORTED_URL_PATTERNS.some(pattern => pattern.test(url));
}

// Helper function to get the target tab (non-extension tab with supported URL)
async function getTargetTabId() {
	const extensionUrl = browser.runtime.getURL('');

	// If manual target is set, verify it still exists and is valid
	if (manualTargetTabId !== null) {
		try {
			const tab = await browser.tabs.get(manualTargetTabId);
			if (tab && !tab.url.startsWith(extensionUrl) && isUrlSupported(tab.url)) {
				return manualTargetTabId;
			}
		} catch (e) {
			// Tab no longer exists, clear manual selection
			manualTargetTabId = null;
		}
	}

	// First try last focused window
	let tabs = await browser.tabs.query({ active: true, lastFocusedWindow: true });
	let targetTab = tabs.find(tab => !tab.url.startsWith(extensionUrl) && isUrlSupported(tab.url));

	if (!targetTab) {
		// Search all windows for an active supported tab
		tabs = await browser.tabs.query({ active: true });
		targetTab = tabs.find(tab => !tab.url.startsWith(extensionUrl) && isUrlSupported(tab.url));
	}

	return targetTab ? targetTab.id : null;
}

// Get list of all available tabs that can be scraped
async function getAvailableTabs() {
	const extensionUrl = browser.runtime.getURL('');
	const allTabs = await browser.tabs.query({});

	return allTabs
		.filter(tab => !tab.url.startsWith(extensionUrl) && isUrlSupported(tab.url))
		.map(tab => ({
			id: tab.id,
			title: tab.title || 'Untitled',
			url: tab.url,
			favIconUrl: tab.favIconUrl,
		}));
}

// Get info about current target tab
async function getTargetTabInfo() {
	const tabId = await getTargetTabId();
	if (!tabId) {
		return { found: false, error: 'No suitable tab found' };
	}

	try {
		const tab = await browser.tabs.get(tabId);
		return {
			found: true,
			id: tab.id,
			title: tab.title || 'Untitled',
			url: tab.url,
			isManual: manualTargetTabId === tabId,
		};
	} catch (e) {
		return { found: false, error: e.message };
	}
}

// Test connection to target tab
async function testTabConnection(tabId) {
	const targetId = tabId || (await getTargetTabId());
	if (!targetId) {
		return { success: false, error: 'No target tab found' };
	}

	try {
		// Send a ping message to the content script
		const response = await browser.tabs.sendMessage(targetId, { ping: true });
		return {
			success: true,
			tabId: targetId,
			response: response || 'Connected',
		};
	} catch (e) {
		return {
			success: false,
			tabId: targetId,
			error: e.message || 'Could not connect to tab',
		};
	}
}

const sendToActiveTab = async function (request, callback) {
	const tabId = await getTargetTabId();
	if (!tabId) {
		console.log("couldn't find target tab to scrape");
		if (callback) callback(null);
		return;
	}
	try {
		const result = await browser.tabs.sendMessage(tabId, request);
		if (callback) callback(result);
	} catch (error) {
		console.warn('sendToActiveTab error:', error.message);
		if (callback) callback(null);
	}
};

browser.runtime.onMessage.addListener(async request => {
	// Get available tabs for scraping
	if (request.getAvailableTabs) {
		return await getAvailableTabs();
	}

	// Get current target tab info
	if (request.getTargetTabInfo) {
		return await getTargetTabInfo();
	}

	// Test connection to target tab
	if (request.testTabConnection) {
		return await testTabConnection(request.tabId);
	}

	// Set manual target tab
	if (request.setTargetTab) {
		if (request.tabId === null || request.tabId === '') {
			manualTargetTabId = null;
			return { success: true, mode: 'auto' };
		}
		manualTargetTabId = parseInt(request.tabId, 10);
		return { success: true, mode: 'manual', tabId: manualTargetTabId };
	}

	// Fetch XML Sitemap (to avoid CORS issues)
	if (request.fetchXMLSitemap) {
		try {
			const response = await fetch(request.url);
			if (!response.ok) {
				return { error: `HTTP error: ${response.status}` };
			}
			const xml = await response.text();
			return { xml };
		} catch (error) {
			return { error: error.message };
		}
	}

	if (request.getStandName) {
		return store.standName;
	}

	if (request.getStorageType) {
		return store.constructor.name;
	}

	if (request.login) {
		return store.initTalismanLogin(request.credential);
	}

	if (request.logOut) {
		return await store.logOut();
	}

	if (request.isAuthorized) {
		const storeData = await store.isAuthorized();
		return storeData ? { data: storeData } : false;
	}

	if (request.createSitemap) {
		if (request.projectId) {
			return store.createSitemap(request.sitemap, request.projectId);
		}
		return store.createSitemap(request.sitemap);
	}

	if (request.saveSitemap) {
		if (request.projectId) {
			return store.saveSitemap(request.sitemap, request.previousSitemapId, request.projectId);
		}
		return store.saveSitemap(request.sitemap, request.previousSitemapId);
	}

	if (request.deleteSitemap) {
		if (request.projectId) {
			return store.deleteSitemap(request.sitemap, request.projectId);
		}
		return store.deleteSitemap(request.sitemap);
	}

	if (request.getAllSitemaps) {
		if (request.projectId) {
			return store.getAllSitemaps(request.projectId);
		}
		return store.getAllSitemaps();
	}

	if (request.getAllProjects) {
		return store.getAllProjects();
	}

	if (request.sitemapExists) {
		if (request.projectId) {
			return store.sitemapExists(request.sitemapId, request.projectId);
		}
		return store.sitemapExists(request.sitemapId);
	}

	if (request.getSitemapData) {
		return store.getSitemapData(Sitemap.sitemapFromObj(request.sitemap));
	}

	if (request.scrapeSitemap) {
		const sitemap = Sitemap.sitemapFromObj(request.sitemap);
		const queue = new Queue();
		const browserTab = new ChromePopupBrowser({
			pageLoadDelay: request.pageLoadDelay,
		});

		const scraper = new Scraper({
			queue,
			sitemap,
			browser: browserTab,
			store,
			startUrl: request.startUrl,
			requestInterval: request.requestInterval,
			requestIntervalRandomness: request.requestIntervalRandomness,
			pageLoadDelay: request.pageLoadDelay,
		});

		return new Promise(resolve => {
			try {
				scraper.run(function () {
					browserTab.close();
					browser.notifications.create('scraping-finished', {
						type: 'basic',
						iconUrl: 'assets/images/icon128.png',
						title: 'Scraping finished!',
						message: `Finished scraping ${sitemap._id}`,
					});
					// table selector can dynamically add columns (addMissingColumns Feature)
					resolve(sitemap.selectors);
				});
			} catch (e) {
				console.log('Scraper execution cancelled', e);
			}
		});
	}

	if (request.previewSelectorData) {
		const tabId = await getTargetTabId();
		if (!tabId) {
			console.log("couldn't find target tab for preview");
			return [];
		}
		try {
			return await browser.tabs.sendMessage(tabId, request);
		} catch (error) {
			console.warn('previewSelectorData error:', error.message);
			return [];
		}
	} else if (request.backgroundScriptCall) {
		return new Promise((resolve, reject) => {
			const backgroundScript = getBackgroundScript('BackgroundScript');
			// TODO change to promises
			const deferredResponse = backgroundScript[request.fn](request.request);
			deferredResponse.then(resolve).catch(reject);
		});
	}
});
